<template>
  <div class="home-page">
    <div class="layout">
      <Layout>
        <Sider ref="sider" hide-trigger collapsible :collapsed-width="0" v-model="isCollapsed">
        <Menu  accordion theme="dark" active-name="1" width="auto" @on-select="handleSelected">
          <Submenu name="1">
            <template slot="title"><Icon type="ios-navigate" /></template>
          </Submenu>

          <Submenu name="2">
            <template slot="title"><Icon type="ios-paper" /></template>
          </Submenu>

          <Submenu name="3">
            <template slot="title"><Icon type="ios-calendar" /></template>
          </Submenu>

          <Submenu name="4">
            <template slot="title"><Icon type="ios-cloudy-night" /></template>
          </Submenu>

          <Submenu name="5">
            <template slot="title">
              <Icon type="ios-albums" /></template>
            <MenuItem name="5-1"></MenuItem>
            <MenuItem name="5-2"></MenuItem>
            <MenuItem name="5-3"></MenuItem>
            <MenuItem name="5-4">IC</MenuItem>
            <MenuItem name="5-5"></MenuItem>
            <MenuItem name="5-6"></MenuItem>
            <MenuItem name="5-7"></MenuItem>
            <MenuItem name="5-8"></MenuItem>
            <MenuItem name="5-9">IC</MenuItem>
            <MenuItem name="5-10"></MenuItem>
            <MenuItem name="5-11"></MenuItem>
          </Submenu>
        </Menu>
        </Sider>
          <div class="collapse-icon">
          <Icon @click.native="collapsedSider" :class="rotateIcon"  type="md-arrow-back" size="24"></Icon>
          </div>
        <Layout :style="warp">
          <Content :style="content">
            <div v-if="show">
              <Divider dashed orientation="left">
                <h2>{{ choose }}</h2>
              </Divider>
              <TableZ @is-check="onIsCheck" ref="tableData"></TableZ>
              <!--  -->
              <div class="button-group">
                <!--  -->
                <Button type="success" @click="addModal = true" icon="md-add"></Button>
                <Modal v-model="addModal">
                  <Divider dashed orientation="left" style="margin-bottom: 30px"></Divider>
                  <Input v-model=value.code size="large" placeholder="" style="margin-bottom: 10px; width:95%" />
                  <Input v-model=value.name size="large" placeholder="" style="margin-bottom: 10px; width:95%" />
                  <div slot="footer">
                    <Button type="text" @click="cancel"></Button>
                    <Button type="success" size="large" @click="add"></Button>
                  </div>
                </Modal>

                <Button type="primary" icon="ios-cloud"></Button>

                <!--  -->
                <Button type="error" @click="deleteModal = true" icon="md-trash"></Button>
                <Modal v-model="deleteModal">
                  <Divider dashed orientation="left" style="margin-bottom: 30px"></Divider>
                  <h2 style="color: rgb(252,13,27)"></h2>
                  <div slot="footer">
                    <Button type="text" @click="cancel"></Button>
                    <Button type="error" size="large" @click="remove"></Button>
                  </div>
                </Modal>

              </div>

            </div>
            <Divider dashed orientation="left" v-else>
              <h2>{{ choose }}</h2>
            </Divider>
          </Content>
        </Layout>
      </Layout>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.button-group {
  margin-top: 10px;
  button {
    margin-right: 10px;
  }
}
.collapse-icon {
  position: fixed;
  left: 6.5rem;
  bottom: 5rem;
  @include size(2rem, 2rem);
  padding: 0;
  background: transparent;
  color: $box-title;
}
.rotate-icon {
  transform: rotate(-180deg);
  color: $box-title-small;
}
</style>
<script>
import TableZ from './TableZ.vue'
import { mapState } from 'vuex'
import { mapActions } from 'vuex'
export default {
  name: 'HomePage',
  components: {
    TableZ
  },
  data: function() {
    return {
      warp: {
        padding: '24px'
      },
      content: {
        padding: '24px',
        minHeight: '100vh',
        background: '#fff',
        border: '1px solid transparent',
        'border-radius': '6px',
        'box-shadow': '0 2px 7px rgba(0,0,0,.15)'
      },
      show: false,
      choose: '',
      deleteModal: false,
      addModal: false,
      isCollapsed: false,
      localPassWord: 'iii',
      index: [],
      value: {
        code: '',
        name: ''
      }
    }
  },
  computed: {
    rotateIcon() {
      return ['menu-icon', this.isCollapsed ? 'rotate-icon' : '']
    },
    ...mapState({
      account: state => state.account,
      accountAlias: 'account',

      passWord: state => state.passWord,
      passWordAlias: 'passWord',

      // state
      updateID(state) {
        return state.id + this.localPassWord
      }
    })
  },
  methods: {
    ...mapActions(['actionUpdatePassWord']),
    handleSelected(name) {
      if (name) {
        if (name === '5-3') {
          this.choose = ''
          this.collapsedSider()
          return (this.show = true)
        }
      }
      this.show = false
      return (this.choose = '')
    },
    collapsedSider() {
      this.$refs.sider.toggleCollapse()
    },
    add() {
      let code = this.value.code
      let name = this.value.name
      this.$refs.tableData.data.push({ code, name })
      this.addModal = false
      this.$Message.success('')
    },
    onIsCheck: function(data, isOk) {
      if (isOk) {
        let index = [...this.index, data]
        this.index = [...new Set(index)]

        // checkboxvuexpassword
        // const newPassWord = { password: '9527' }
        // this.actionUpdatePassWord(newPassWord).then(() => {
        //   this.$Message.success('')
        //   console.log(this.passWord)
        // })
      }
      if (isOk === false) {
        this.index = this.index.filter(e => e !== data)
      }
    },
    remove() {
      let data = this.index
        .concat(this.$refs.tableData.data)
        .filter(function(v, i, arr) {
          return arr.indexOf(v) === arr.lastIndexOf(v)
        })
      if (data) {
        this.$refs.tableData.data = data
      }
      this.deleteModal = false
      this.$Message.success('')
    },
    cancel() {
      if (this.addModal) {
        this.addModal = false
      }
      if (this.deleteModal) {
        this.deleteModal = false
      }
    }
  }
}
</script>
